.toggle-button {
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    padding: 0;
    box-sizing: border-box;
}
.toggle-button:focus {
    outline: none;
    cursor: pointer;
}
.toggle-button_line {
    width: 30px;
    height: 2px;
    background: black;
}
@media (min-width: 769px) {
    .toggle-button {
        display: none;
    }
}
.toolbar {
  position: fixed;
  width: 100%;
  height: 70px;
  top: 0;
  left: 0;
}
.space {
  flex: 1 1;
}
.toolbar__navigation {
  display: flex;
  height: 100%;
  padding: 0 1rem;
  align-items: center;
  color: black;
  padding-right: 50px;
}
.toolbar__logo {
  margin-left: 1rem;
  padding-top: 20px;
}
.toolbar__logo a {
  color: black;
  font-size: 40px;
  font-family: 'Satisfy', cursive;
  text-decoration: none;
  font-size: 40px;
  padding-left: 30px;
}
.toolbar_navigation-items ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.toolbar_navigation-items li {
  padding: 0 1.0rem;
  list-style-type: none;
}
.toolbar_navigation-items a {
  font-family: Noto Sans TC;
  font-size: 25px;
  color: black;
  text-decoration: none;
}
.toolbar__logo a:hover,
.toolbar__logo a:active,
.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #f442e5;
  transition: color .5s
}
@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
}
@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }
  .toolbar__logo {
    margin-left: 0;
  }
}
.side-drawer {
    height: 100%;
    background: white;
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}
.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.side-drawer ul {
    height: 100%;
    text-decoration: none;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.side-drawer li {
    margin: 0.5rem 0;
}
.side-drawer a {
    font-family: Noto Sans TC;
    font-size: 30px;
    color: black;
    transition: #f442e5 .5s
}
.side-drawer a:hover,
.side-drawer a:active {
    color: #f442e5;
    transition: color .5s
}
@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}
